<template>
  <div class="part">
    <div class="part-title-wrap">
      <h3 class="part-title">{{type}}</h3>
      <ASwitch :disabled="!optional" :checked="active" v-on:change="toggle"/>
    </div>
    <div class="part-listings">
      <ARow v-bind:gutter="12">
        <ACol
          v-bind:xs="24"
          v-bind:sm="12"
          v-bind:md="8"
          v-bind:lg="6"
          v-for="{group, link, name, price, selector} of listings"
        >
          <Listing
            v-bind:group="group"
            v-bind:name="name"
            v-bind:price="price.toFixed(2)"
            v-bind:link="link"
            v-bind:selector="selector"
            v-bind:selected="selected"
            @change="setSelected"
          />
        </ACol>
      </ARow>
    </div>
  </div>
</template>

<script>
import Listing from "./Listing";

export default {
  components: { Listing },
  data: function() {
    return {
      selected: (
        this.listings.find(l => l.selected === true) || this.listings[0]
      ).selector
    };
  },
  methods: {
    toggle: function(checked) {
      this.$emit("toggle", this.type, checked);
    },
    setSelected: function(selector) {
      this.selected = selector;
      this.$emit("change", this.type, selector);
    }
  },
  name: "PartType",
  props: {
    active: Boolean,
    icon: String,
    listings: Array,
    optional: Boolean,
    type: String
  }
};
</script>

